
body {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
}
.main-site{
  min-height: 100vh;
  max-width: 100vw;
  background-image: url(/static/img/bg.721e59d.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.main-site .description-container{
  position: absolute;
  text-align: center;
  padding: 2em 2em;
  width: 40%;
  top: 30%;
  left: 30%;
  background-color: rgba(0, 0, 0, 0.8);
  color: rgb(216, 90, 31);
  font-size: 1.2em;
  font-weight: bold;
}

#header[data-v-b114b4ec]{
    height: 2em;
    background-color: black;
}
.navbar[data-v-b114b4ec]{
    float: right;
}
a > .navbar-item[data-v-b114b4ec]{
    text-decoration: none;
    color: white;
    font-weight: bold;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
}
a:hover > .navbar-item[data-v-b114b4ec]{
    cursor: pointer;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.navbar-item[data-v-b114b4ec]{
